@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;700&display=swap);
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Poppins", sans-serif;
}

/* Rules for sizing the icon. */
.md-18 {
    font-size: 18px;
}
.md-24 {
    font-size: 24px;
}
.md-36 {
    font-size: 36px;
}
.md-48 {
    font-size: 48px;
}

/* Rules for using icons as black on a light background. */
.md-dark {
    color: rgba(40, 44, 52, 0.54);
}
.md-dark-inactive {
    color: rgba(40, 44, 52, 0.26);
}

/* Rules for using icons as white on a dark background. */
.md-light {
    color: rgba(236, 239, 243, 1);
}
.md-light-inactive {
    color: rgba(236, 239, 243, 0.3);
}

main {
    padding: 80px;
}

.gallery {
    width: 1112px;
    overflow: scroll;
}

.gallery::-webkit-scrollbar {
    display: none;
}

.gallery__row {
    display: flex;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.gallery__title {
    display: flex;
    align-items: center;

    margin-bottom: 40px;
}

.gallery__title h2 {
    font-style: normal;
    font-weight: bold;
    font-size: 20px;
    line-height: 135%;

    color: #282c34;
    padding-right: 20px;
}

.gallery__title p {
    font-style: normal;
    font-weight: bold;
    font-size: 12px;
    line-height: 17px;

    color: #c678dd;
}

.header {
    width: 100%;
    height: 120px;
    padding: 0px 60px;

    background: linear-gradient(
        113.3deg,
        #535a68 0%,
        #3c414c 42.54%,
        #272b33 100%
    );
    opacity: 0.95;

    display: flex;
    justify-content: space-between;
    align-items: center;
}

.header__section {
    display: flex;
    align-items: center;
}

.header__logo {
    width: 202px;
    margin-right: 80px;
}

.Link {
    margin-right: 40px;

    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 17px;

    color: #eceff3;
    text-decoration: none;
}

.Link--active {
    color: #c678dd;
    text-decoration: none;
}

.inputSearch {
    width: 400px;
    height: 45px;

    position: relative;
}

.inputSearch input {
    width: 100%;
    height: 100%;
    border-radius: 50px;
    background: #eceff3;
    outline: none;
    border: none;
    padding: 8 px;
    padding-left: 52px;
}

.inputSearch__Icon {
    position: absolute;
    left: 0;
    -webkit-transform: translate(50%, 50%);
            transform: translate(50%, 50%);
}

.AnimeCard {
    width: 248px;
    height: 351px;
    border-radius: 15px;
    position: relative;
    margin-right: 20px;
}

.AnimeCard:hover {
    box-shadow: inset 0px 0px 25px #f3ecf3;
    cursor: pointer;
}

.AnimeCard__img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 15px;
}

.AnimeCard__figcaption {
    width: 100%;
    height: 60px;
    padding: 8px;
    background: rgba(14, 14, 44, 0.7);
    border-radius: 0px 0px 15px 15px;

    position: absolute;
    bottom: 0px;

    display: flex;
    align-items: center;
    justify-content: center;

    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 135%;

    text-align: center;

    color: #eceff3;
}

.AnimeForm h2 {
    margin-top: 80px;
    margin-bottom: 20px;
}

.input {
    display: block;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 17px;

    color: #282c34;
    margin-bottom: 32px;
}

.input input {
    display: block;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 135%;
    margin-top: 12px;

    color: #282c34;
    padding: 12px;

    border: 1px solid #282c34;
    box-sizing: border-box;
    border-radius: 10px;
    outline: none;
}

.input input:focus-visible {
    color: #282c34;
    border: 2px solid #c678dd;
}

button {
    min-width: 140px;
    height: 48px;
    padding: 8px;
    background: #282c34;
    border-radius: 30px;
    outline: none;
    border: none;

    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 135%;

    color: #eceff3;
}

button:hover {
    -webkit-filter: brightness(140%);
            filter: brightness(140%);
    box-shadow: 0px 4px 4px rgba(40, 44, 52, 0.25);
}

