.header {
    width: 100%;
    height: 120px;
    padding: 0px 60px;

    background: linear-gradient(
        113.3deg,
        #535a68 0%,
        #3c414c 42.54%,
        #272b33 100%
    );
    opacity: 0.95;

    display: flex;
    justify-content: space-between;
    align-items: center;
}

.header__section {
    display: flex;
    align-items: center;
}

.header__logo {
    width: 202px;
    margin-right: 80px;
}
